



























































































import { Component, Vue } from 'vue-property-decorator';
import GridComponent from './components/Grid.vue';
import Tile from './models/Tile';

const FPS = 60;

@Component({
  components: {
    Grid: GridComponent,
  },
})
export default class App extends Vue {
  tiles: Tile[] = [
    new Tile(0, 0),
    new Tile(1, 0),

    new Tile(0, 1),
    new Tile(1, 1),
  ];

  time = Date.now();

  mounted() {
    setInterval(() => {
      // Time in seconds
      this.time = Date.now() / 1000;
    }, 1000 / FPS);
  }
}
